    function handleSendData() {
        let missingData = checkMissingData();
        if (missingData !== "none") {
            setMessageParams({ type: "error", message: `No ha suministrado el campo: ${missingData}` })
            setOpenMessage(true)
        } else {
            let data = getData();

            fetch("/student", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Accept": "*/*"
                },
                body: JSON.stringify(data)
            })
                .then(answer => answer.json())
                .then(response => {
                    if(response.error){
                        setMessageParams({ type: "error", message: response.error })
                        setOpenMessage(true)
                    }else{
                        setMessageParams({ type: "success", message: `Se ha inscrito satisfactoriamente al estudiante` })
                        cleanData();
                        setOpenMessage(true)
                    }

                })
                .catch(err=>{
                    setMessageParams({ type: "error", message: `Ocurri un error, no se ha inscrito al estudiante` })
                    setOpenMessage(true)
                })
        }
    }


